import React from 'react';


const SlotM = (props) => {
    // let x = 'A';
    // let y = 'A';
    // let z = 'A';


    // let x = props.x;
    // let y = props.y;
    // let z = props.z;


    let { x, y, z } = props;
    // object Destructuring


    if (props.x === props.y && props.y === props.z) {
        return (
            <>
                <div className="Slot_machine">

                    <h1> {x}{y}{z} </h1>
                    <h1>This is maching</h1>
                    <hr />

                </div>


            </>
        );

    } else {

        return (
            <>
                <div className="Slot_machine">

                    <h1> {x}{y}{z} </h1>
                    <h1>This is not maching</h1>
                    <hr />

                </div>


            </>
        );

    }

};


export default SlotM;